<template>
  <div class="build-guide">
    <headerTop :title="title"
               :router="router"></headerTop>
    <div class="main">
      <div class="item">
        <text class="title"></text>
        <text class="content">1. <br />2. <br />3.  5  6 <br /> 4  2.5 (<br />)</text>
      </div>
      <div class="item">
        <text class="title"> </text>
        <text class="content">1. <br />2. </text>
      </div>
      <div class="item">
        <text class="title"> </text>
        <text class="content">1.  30mA RCD</text>
        <div class="content-wrapper">
          <text class="content content-text">2. /</text>
          <image src="src/assets/images/9.png"
                 style="width:175px;height:280px;align-self:center;" />
        </div>
        <text class="content">3. </text>
      </div>
    </div>
  </div>
</template>

<script>
import headerTop from '../../Common/headerTop'
export default {
  components: {
    headerTop
  },
  data () {
    return {
      title: '',
      router: '/builder'
    }
  }
}
</script>

<style scoped>
.build-guide {
  width: 750px;
  min-height: 100vh;
}
.main {
  border-top: 4px solid #e5e5e5;
  padding-top: 39px;
  padding-right: 52px;
  padding-left: 65px;
}
.title,
.content {
  font-family: MicrosoftYaHei-Bold;
  color: rgba(66, 66, 66, 1);
  line-height: 40px;
}
.title {
  font-size: 28px;
  letter-spacing: 2.8px;
  font-weight: bold;
}
.content {
  font-size: 24px;
  letter-spacing: 2.4px;
  font-weight: regular;
}
.item {
  margin-bottom: 34px;
}
.content-wrapper {
  flex-direction: row;
  justify-content: space-between;
}
.content-text {
  width: 419px;
}
</style>



