<template>
  <div>
    <div id="bmap"
         :style="style">
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      style: {
        width: '100%',
        height: this.height + 'px'
      }
    }
  },
  props: {
    height: {
      type: Number,
      default: 463
    },
    // 
    longitude: {
      type: Number,
      default: 114.1436167638
    },
    // 
    latitude: {
      type: Number,
      default: 30.6328571114
    }
  },
  mounted () {
    var map = new window.BMap.Map('bmap') // Map
    var point = new window.BMap.Point(this.longitude, this.latitude) // Map,
    map.centerAndZoom(point, 18)
    map.setCurrentCity('') // 
    map.addControl(new window.BMap.MapTypeControl({
      mapTypes: [ // 
        window.BMAP_NORMAL_MAP,
        window.BMAP_HYBRID_MAP
      ] }))
    // var overView = new window.BMap.OverviewMapControl()
    // var overViewOpen = new window.BMap.OverviewMapControl({ isOpen: true, anchor: BMAP_ANCHOR_BOTTOM_RIGHT })
    // map.addControl(overView) // 
    // map.addControl(overViewOpen) // 

    // setTimeout(function () {
    //   map.setZoom(14)
    // }, 2000) // 214

    map.enableScrollWheelZoom(true) // 

    var topLeftControl = new window.BMap.ScaleControl({ anchor: window.BMAP_ANCHOR_TOP_LEFT })// 
    var topLeftNavigation = new window.BMap.NavigationControl() // 
    // var top_right_navigation = new window.BMap.NavigationControl({ anchor: BMAP_ANCHOR_TOP_RIGHT, type: BMAP_NAVIGATION_CONTROL_SMALL }) // 
    map.addControl(topLeftControl)
    map.addControl(topLeftNavigation)
    // map.addControl(top_right_navigation)

    var marker = new window.BMap.Marker(point)// 
    map.addOverlay(marker)
    // var driving = new window.BMap.DrivingRoute(map, {
    //   renderOptions: {
    //     map: map,
    //     autoViewport: true
    //   }
    // })
    // driving.search('1', '')
  }
}
</script>



