<template>
  <div class="must-know">
    <headerTop :title="title"
               :router="router"></headerTop>
    <div class="main">
      <div class="item"
           v-for="(item,index) in text"
           :key="index">
        <text class="title">{{item.title}}</text>
        <text class="content">{{item.content}}</text>
      </div>
    </div>
  </div>
</template>

<script>
import headerTop from '../../Common/headerTop'
export default {
  components: {
    headerTop
  },
  data () {
    return {
      title: '',
      router: '/exhibitor',
      text: [
        {
          title: '1',
          content: ''
        },
        {
          title: '2',
          content: ''
        },
        {
          title: '3',
          content: ''
        },
        {
          title: '4',
          content: '/'
        },
        {
          title: '5',
          content: '6 6  25  15:00~24:006 25  15:00 '
        }
      ]
    }
  }
}
</script>

<style scoped>
.must-know {
  width: 750px;
  min-height: 100vh;
}
.main {
  border-top: 4px solid #e5e5e5;
  padding-top: 39px;
  padding-right: 32px;
  padding-left: 65px;
}
.title,
.content {
  font-family: MicrosoftYaHei-Bold;
  color: rgba(66, 66, 66, 1);
  line-height: 40px;
}
.title {
  font-size: 28px;
  letter-spacing: 2.8px;
  font-weight: bold;
}
.content {
  font-size: 24px;
  letter-spacing: 2.4px;
  font-weight: regular;
}
.item {
  margin-bottom: 80px;
}
</style>



